<template>
  <div class="forgetPasswd">
    <mt-header>
      <router-link :to="{path:'/'}" slot="left">
        <mt-button icon="back">重置密码</mt-button>
      </router-link>
    </mt-header>
    <div class="forgetPasswdBody">
      <a class="field" style="margin-top:10%;" :style="{borderBottom: phoneborderBottom}">
        <div class="wraper">
          <div class="value">
            <span>+86</span>
            <span style="margin-right:10px;margin-left:2px;"> | </span>
            <input v-model="username" @focus="focusInput('phoneborderBottom')" @blur="blurInput('phoneborderBottom')" placeholder="输入手机号" type="text" class="core">
          </div>
        </div>
      </a>
      <a class="field diff" style="margin-top:10%;" :style="{borderBottom: authborderBottom}">
        <div class="wraper">
          <div class="value">
            <input v-model="authCode" @focus="focusInput('authborderBottom')" @blur="blurInput('authborderBottom')" placeholder="输入验证码" type="text" class="core">
          </div>
          <mt-button class="authBtn" @click.native="getAuthCode" type="primary">获取验证码</mt-button>
        </div>
      </a>
      <a class="field" :style="{borderBottom: passwdBottom}">
          <div class="img"><img :src="passwdSrc" alt=""></div>
          <div class="wraper">
            <div class="value">
              <input v-model="newPassword" v-if="show" @focus="passwdFocus" @blur="passwdBlur" placeholder="请输入6-18位的重置密码" type="text" class="core">
              <input v-model="newPassword" v-else @focus="passwdFocus" @blur="passwdBlur" placeholder="请输入6-18位的重置密码" type="password" class="core">
            </div>
          </div>
          <div class="img" @click="showPasswd" style="margin-right:5%;"><img :src="passwdEyeSrc" alt=""></div>
        </a>
      <mt-button class="btn" @click.native="finish" type="primary" size="large">完成</mt-button>
    </div>
  </div>  
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import {checkError} from '../utils/util';
import {Toast} from 'mint-ui';
export default {
  name: 'forgetPasswd',
  data () {
    return {
      username: '',
      authCode: '',
      newPassword: '',
      phoneborderBottom: '',
      authborderBottom: '',
      passwdSrc: '/static/passwd.png',
      passwdEyeSrc: '/static/passwdHide.png',
      show: false,
      passwdBottom: ''
    }
  },

  methods: {
    ...mapActions({
      getAuthCodeFn: 'getAuthCode',
      validateAuthCode: 'validateAuthCode',
      resetPasswd: 'resetPasswd'
    }),
    focusInput: function(name) {
      
      this[name] = '2px solid #d06091';
    },
    blurInput: function(name) {
      this[name] = '1px solid #e3dee1';
    },
    passwdFocus: function() {
        this.passwdSrc = '/static/passwdNow.png';
        this.passwdBottom = '2px solid #d06091'
    },
    passwdBlur: function() {
      this.passwdSrc = '/static/passwd.png';
      this.passwdBottom = '1px solid #e3dee1';
    },
    showPasswd: function() {
      this.passwdSrc = '/static/passwdNow.png';
      if(this.passwdEyeSrc == '/static/passwdHide.png') {
        this.passwdEyeSrc ='/static/passwdShow.png';
        this.show = true;
      } else {
        this.passwdEyeSrc ='/static/passwdHide.png';
        this.show = false;
      }
    },
    getAuthCode: function() {
      let data = {};
      data.action = 'resetPassword';
      data.mobile = this.username;
      this.getAuthCodeFn(data).then(response => {
      },error => {
        if(error.body.code == 5001) {
            Toast({
              message: '验证码类短信1小时内同一手机号发送次数不能超过3次',
              position: 'bottom',
              duration: 2000
            });
          }
      });
    },
    finish: function() {
      let data = {};
      data.action = 'resetPassword';
      data.mobile = this.username;
      data.authCode = this.authCode;
      this.validateAuthCode(data).then(response => {
        
        let data = {};
        data.username = this.username;
        data.newPassword = this.newPassword;
        this.resetPasswd(data).then(response => {
          this.$router.push({path: '/'});
        })
      }, error => {
          const msg = checkError(error);
          if(msg) {
             Toast({
                message: msg,
                position: 'bottom',
                duration: 2000
              });
          }
      })
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.forgetPasswd .forgetPasswdBody {
  padding: 2%;
}
.forgetPasswd .mint-header {
  background-color: #fff;
  color: #000000;
}
.forgetPasswd .mint-header label {
  margin-left: 2%;
}
.authCode {
  display: flex;
  justify-content: space-between;
}
.forgetPasswd .authBtn {
  box-shadow: none;
  background-color: #fff;
  border: 1px solid #d06091;
  color: #d06091;
}
.forgetPasswd .mint-button.btn{
  margin-top: 20%;
  background-color: #fff; 
  border-radius: 8px;
  border: 1px solid #dad2da;
  color: #d06091;
}
.forgetPasswd .mint-button::after {
  background-color: #f1cfde;
}
.forgetPasswd .mint-cell-text {
  font-size: 0.4rem;
}
.forgetPasswd .mint-button-text {
  font-size: 0.4rem;
  display: inline-block;
  padding-top: 10px;
}
.forgetPasswd .mint-button {
  height: 1rem;
}
</style>



